<template>
    <div class="article">
        <h1>{{article.title}}</h1>
        <div class="cont" v-html="article.content">

        </div>
        <div class="art-bottom">
            {{article.create_time | date}}
        </div>
    </div>
</template>
<script>
import { getArticle } from '@/api'
export default {
    data () {
        return {
            article: {}
        }
    },
    mounted() {
        let { cateid, cateindex } = this.$route.query
        this.getArtData(cateid, cateindex)
    },
    methods: {
        async getArtData (cateid, cateindex) {
            this.article = await getArticle(cateid, cateindex)
        },
    },
    filters: {
        date (t) {
            t = t ? new Date(Number(t)) : new Date(1545977032592)
            let reg = /\//g
            return t.toLocaleDateString().replace(reg, '-')
        }
    }
}
</script>
<style lang="less" scoped>
.article{
    h1{
        margin: 20px 0;
        color: #e0c073;
        font-weight: normal;
    }
    .empty{
        padding: 1px 0 ;
    }
    .cont{
        padding: 10px 30px 50px 30px;
        text-align: left;
        // text-indent: 2em;
        line-height: 45px;
        font-size: 17px;
    }
    .art-bottom{
        border-top: 1px solid #e0c073;
        padding: 20px;
        text-align: right;
    }
}
</style>



